import {serverUrl} from "./helpers.js";
import MandatumApp from "./product-app.js";
import MandatumOrder from "./checkout-app.js";
async function main() {
  let MandatumInstance, OrderInstance, productContainer, shopifyProduct, productID, productInfo, isMandatum, descuento, dias, activeWidget, isMandatumOrder, mandatumCheckoutDiscount, mandatumCheckoutPrice, discountCode;
  const isProduct = location.pathname.includes("products");
  const isOrder = location.pathname.includes("orders");
  const isCheckout = location.pathname.includes("checkouts");
  let scriptShopify = document.querySelector("script[src*='mandatum']");
  let queryString = new URLSearchParams(scriptShopify.src.split("?")[1]);
  let shopName = queryString.get("shop");
  if (isProduct) {
    console.log("Is Product");
    productContainer = document.querySelector("body");
    shopifyProduct = await fetch(`${location.href.split("?")[0]}.json`).then((json) => json.json());
    productID = shopifyProduct.product.id;
    productInfo = await fetch(`https://${serverUrl}/isMandatum?shop=${shopName}&product=${"gid://shopify/Product/" + productID}`).then((json) => json.json());
    isMandatum = productInfo.isMandatum;
    descuento = parseFloat(productInfo.descuento);
    dias = parseInt(productInfo.dias);
    activeWidget = true;
  }
  console.log("------------------");
  console.log(isMandatum);
  console.log(isProduct);
  console.log("------------------");
  if (isMandatum && isProduct) {
    const newShopProd = productInfo.newProduct;
    console.log("product", newShopProd);
    MandatumInstance = new MandatumApp(productContainer, shopName, descuento, dias, productID, newShopProd);
    MandatumInstance.init();
  }
  if (isOrder || isCheckout) {
    console.log("Is Order");
    const mandateProductFound = Shopify.checkout.line_items.find((prod) => {
      return prod.properties["Mandatum Discount"] ? true : false;
    });
    const some = mandateProductFound.discount_allocations.find((element) => {
      discountCode = element.description;
      return element.description.includes("mandatum");
    });
    isMandatumOrder = mandateProductFound ? true : false;
    mandatumCheckoutDiscount = parseFloat(mandateProductFound.properties["Mandatum Discount"].split("%")[0]) / 100;
    mandatumCheckoutPrice = parseFloat(mandateProductFound.line_price);
  }
  console.log("********************");
  console.log(isOrder);
  console.log(isMandatumOrder);
  console.log("********************");
  if (isOrder && isMandatumOrder) {
    console.log("Is mandatum Order", mandatumCheckoutDiscount);
    console.log(mandatumCheckoutPrice);
    const shopInfo = await fetch(`https://${serverUrl}/checkoutData?shop=${shopName}`).then((json) => json.json());
    OrderInstance = new MandatumOrder(discountCode, mandatumCheckoutDiscount, mandatumCheckoutPrice, shopInfo.name);
    await OrderInstance.init();
  }
  return {
    product: MandatumInstance,
    order: OrderInstance
  };
}
main().then((App) => {
  window.mandatum = App;
});
